@import "country-dropdown.assets.scss";
@import "perfect-scrollbar.scss";


$alias: 'WooZone-NAWS-';

##{$alias}wrapper {
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  float: left;
  width: 100%;

  .#{$alias}quick-menu {
    position: fixed;
    top: 100%;
    right: 0;
    background: #fff;
    width: 250px;
    margin-top: -32px;
    z-index: 99999;
    padding: 10px;
    padding-top: 32px;

    &.__is_opened {
      bottom: 0;
      top: auto;
    }

    h2 {
      margin: 0;
      padding: 0;
      background: #315898;
      color: #fff;
      font-size: 12px;
      display: block;
      padding: 8px 10px;
      text-align: center;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      font-weight: bold;
      text-transform: uppercase;

      span {
        margin-right: 5px;
        font-size: 14px;
        line-height: 20px;
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        padding: 5px;
        box-sizing: content-box;
        background: #374a87;
      }

      &:hover {
        background: #374a87;
        cursor: pointer;
      }
    }

    p {
      font-size: 11px;
      color: #424242;
    }

    ul {
      margin: 0;
      padding: 0;

      li {
        padding: 0;
        margin: 0;

        a {
          background: #3d3f7a;
          display: block;
          padding: 5px 10px;
          color: #fff;
          font-size: 12px;
          margin-bottom: 5px;
          cursor: pointer;
          border: 1px solid #3d3f7a;
          border-radius: 4px;


          &:hover {
            background: #296cae;
          }
        }
      }
    }
  }

  .#{$alias}search-form {
    display: grid;
    grid-template-columns: 200px 200px 1fr 150px 170px;
    grid-gap: 20px;

    .#{$alias}custom-select {
      display: inline-block;
      height: 45px;
      background: #fff;
      border: 1px solid #6995dc;
      border-radius: 5px;
      position: relative;
      cursor: pointer;
      color: #1e1e1e;

      &:hover {
        background: #315898;
        color: #fff;

        &:after {
          color: #fff;
        }
      }

      select {
        option {
          color: #1e1e1e;
        }
      }
    }

    .#{$alias}custom-select:after {
      font-family: dashicons;
      content: "\F347";
      display: block;
      position: absolute;
      right: 12px;
      top: 13px;
      z-index: 8;
      font-size: 13px;
      font-weight: bold;
      color: #315898;
    }

    select {
      z-index: 9;
      padding: 12px 15px;
      margin: 0;
      height: auto;
      border-radius: 5px;
      width: 100%;
      font-weight: bold;
      box-shadow: none !important;
      cursor: pointer;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      position: relative;
      display: block;
      border: none;
      padding-right: 30px;
      position: absolute;
      background: transparent;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }

    input {
      border: 1px solid #d9d9d9;
      padding: 2px 15px;
      border-radius: 2px;
    }

    .AZON-country-list-default {
      width: 100%;
    }
    .notice {
      padding: 10px;
      margin: 0;
    }

    textarea {
      position: absolute;
      top: 20px;
      right: 20px;
      width: 300px;
      height: 200px;
      display: none;
    }

    .wz-search-btn {
      background: #26b36c;
      border-radius: 5px;
      color: #fff;
      font-size: 12px;
      padding: 10px;
      display: inline-block;
      border: none;
      font-weight: bold;
      text-transform: uppercase;

      &:hover {
        background: #000;
      }
    }

  }

  .#{$alias}loader {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 99;
    background: rgba( 0,0,0, 0.5 );
    //border: 1px solid red;

    .lds-roller {
      display: inline-block;
      position: relative;
      width: 64px;
      height: 64px;
      position: absolute;
      top: 20%;
      left: 50%;
      margin-left: -32px;
      div {
        animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        transform-origin: 32px 32px;
        &:after {
          content: " ";
          display: block;
          position: absolute;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background: #fff;
          margin: -3px 0 0 -3px;
        }
        &:nth-child(1) {
          animation-delay: -0.036s;
          &:after {
            top: 50px;
            left: 50px;
          }
        }
        &:nth-child(2) {
          animation-delay: -0.072s;
          &:after {
            top: 54px;
            left: 45px;
          }
        }
        &:nth-child(3) {
          animation-delay: -0.108s;
          &:after {
            top: 57px;
            left: 39px;
          }
        }
        &:nth-child(4) {
          animation-delay: -0.144s;
          &:after {
            top: 58px;
            left: 32px;
          }
        }
        &:nth-child(5) {
          animation-delay: -0.18s;
          &:after {
            top: 57px;
            left: 25px;
          }
        }
        &:nth-child(6) {
          animation-delay: -0.216s;
          &:after {
            top: 54px;
            left: 19px;
          }
        }
        &:nth-child(7) {
          animation-delay: -0.252s;
          &:after {
            top: 50px;
            left: 14px;
          }
        }
        &:nth-child(8) {
          animation-delay: -0.288s;
          &:after {
            top: 45px;
            left: 10px;
          }
        }
      }
    }

    @keyframes lds-roller {
      0% {
        transform: rotate(0deg);
      }

      100% {
        transform: rotate(360deg);
      }
    }
  }

  .#{$alias}search-results {
    margin-top: 30px;

    h2 {
      padding: 0;
      margin: 0;
      padding-bottom: 20px;
      text-transform: uppercase;
      color: #212121;
      display: none;
      font-size: 15px;
    }

    ul {
      display: grid;
      grid-template-columns: repeat( 4, 1fr );
      grid-gap: 20px;

      li {
        border: 1px solid #F5F5F5;
        padding: 20px;
        position: relative;
        margin: 0;

        &:hover {
          background: #F5F5F5;
        }

        > a {
          display: block;
          text-align: center;
          background: #fff;
        }

        h5 {
          margin: 0;
          margin-top: 15px;
          display: block;
          font-size: 12px;

          a {
            color: #6A1B9A;

            &:hover {
              color: #9C27B0;
            }
          }
        }

        h4 {
          margin: 0;
          margin-top: 5px;
          margin-bottom: 5px;
        }

        .#{$alias}price {
          .a-price-symbol {
            top: -4px;
            font-size: 11px;
            position: relative;
            margin-right: 1px;
          }
          .a-price-fraction {
            top: -4px;
            font-size: 11px;
            position: relative;
            margin-left: 1px;
          }
          .a-price-decimal {
            display: none;
          }
          .a-offscreen {
            display: none;
          }

          > span:last-child {
            font-size: 16px;
            font-weight: bold;
          }
        }

        .#{$alias}already-imported {
          border: 1px solid #fd7171;
          background: #fd7171;
          padding: 5px 10px;
          color: #fff;
          font-size: 12px;
          border-radius: 3px;
          text-transform: uppercase;
          font-weight: bold;
        }
        .#{$alias}loader {
          .lds-roller {
            top: 50%;
            margin-top: -32px;
          }
        }

        button {
          border: 1px solid #3ab713;
          position: absolute;
          display: block;
          top: 50%;
          left: 50%;
          font-size: 12px;
          padding: 8px 10px;
          background: #3ab713;
          color: #fff;
          width: 140px;
          margin-left: -60px;
          text-transform: uppercase;
          font-weight: bold;
          border-radius: 3px;

          &:hover {
            background: #39c474;
          }
        }

        .#{$alias}data-loaded {
          background: #d32f2f;
          border: 1px solid #b71c1c;
          padding: 4px 10px;
          color: #fff;
          margin-top: 20px;
          display: block;
          text-align: center;
        }
      }
    }

    .#{$alias}pagination-wrapper {
      text-align: center;
    }

    .#{$alias}pagination {
      display: block;
      margin: 20px 0;
      margin-left: 1px;

      li {
        display: inline-block;
        padding: 0;
        margin-left: -1px;

        a {
          padding: 10px 15px;
          background: none;
          color: #212121;
        }

        &.previous,
        &.next {
          border: none;

          &.disabled {
            a {
              color: #E0E0E0;
            }
          }
        }

        &.break-me {
          a {
            color: #9E9E9E;
          }
        }

        &._is_selected {
          background: #3e3b75;
          border: 1px solid #f2f2f2;

          a {
            color: #fff;
            font-weight: bold;
          }
        }
      }
    }
  }

  .#{$alias}import-form {
    margin-top: 40px;
    display: block;
    display: grid;
    grid-template-columns: repeat( 4, 1fr);
    grid-gap: 25px;

    select {
      width: 100%;
      margin-bottom: 10px;
    }

    p {
      font-size: 12px;
      color: #BDBDBD;
      margin: 0;
      font-style: italic;
      margin-bottom: 20px;
    }

    .#{$alias}setting {
      border-right: 1px solid #2f2f2f;
      padding-right: 25px;

      &:last-child {
        border: none;
      }

      .components-base-control__field {
        .components-checkbox-control__input {
          margin: 0;
          position: relative;
          bottom: 3px;
        }
        .components-checkbox-control__label {
          padding-left: 10px;
          color: #fff;
          font-weight: normal;
        }
      }
      .components-range-control .components-base-control__field {
        display: grid;
        grid-template-columns: 1fr 55px;
        grid-gap: 15px;
      }
      .components-range-control__slider {
        -webkit-appearance: none;
        vertical-align: middle;
        outline: none;
        border: none;
        padding: 0;
        background: none;
      }

      .components-range-control__slider::-webkit-slider-runnable-track {
        background-color: #d7dbdd;
        height: 6px;
        border-radius: 3px;
        border: 1px solid transparent;
      }

      .components-range-control__slider[disabled]::-webkit-slider-runnable-track {
        border: 1px solid #d7dbdd;
        background-color: transparent;
        opacity: 0.4;
      }

      .components-range-control__slider::-moz-components-range-control__slider-track {
        background-color: #d7dbdd;
        height: 6px;
        border-radius: 3px;
        border: none;
      }

      .components-range-control__slider::-ms-track {
        color: transparent;
        border: none;
        background: none;
        height: 6px;
      }

      .components-range-control__slider::-ms-fill-lower {
        background-color: #d7dbdd;
        border-radius: 3px;
      }

      .components-range-control__slider::-ms-fill-upper {
        background-color: #d7dbdd;
        border-radius: 3px;
      }

      .components-range-control__slider::-ms-tooltip { display: none; /* display and visibility only */ }

      .components-range-control__slider::-moz-components-range-control__slider-thumb {
        border-radius: 20px;
        height: 18px;
        width: 18px;
        border: none;
        background: none;
        background-color: #606670;
      }

      .components-range-control__slider:active::-moz-components-range-control__slider-thumb {
        outline: none;
      }

      .components-range-control__slider::-webkit-slider-thumb {
        -webkit-appearance: none !important;
        border-radius: 100%;
        background-color: #606670;
        height: 18px;
        width: 18px;
        margin-top: -7px;
      }

      .components-range-control__slider[disabled]::-webkit-slider-thumb {
        background-color: transparent;
        border: 1px solid #d7dbdd;
      }

      .components-range-control__slider:active::-webkit-slider-thumb {
        outline: none;
      }

      .components-range-control__slider::-ms-thumb {
        border-radius: 100%;
        background-color: #606670;
        height: 18px;
        width: 18px;
        border: none;
      }

      .components-range-control__slider:active::-ms-thumb {
        border: none;
      }

      .#{$alias}field-threads {

        .components-base-control__field {
          display: block;

          label {
            display: block;
            font-weight: bold;
            color: #fff;
            padding-top: 20px;
          }

          .components-range-control__slider {
            width: 72%;
            margin-right: 8%;
            display: inline-block;
          }
          .components-range-control__number {
            width: 20%;
            display: inline-block;
          }
        }
      }

      .components-range-control__number {
        border: 1px solid #616161;
        background: #212121;
        color: #000;
        border-radius: 5px;
      }
    }

    .#{$alias}stop-btn,
    .#{$alias}import-btn {
      border: 1px solid #3ab713;
      background: #3ab713;
      color: #fff;
      padding: 8px;
      border-radius: 5px;
      text-transform: uppercase;
      font-weight: bold;

      &:hover {
        border: 1px solid #39c474;
        background: #39c474;
      }

      &.#{$alias}stop-btn {
        background: #ef5350;
        border: 1px solid #f44336;

        &:hover {
          border: 1px solid #b71c1c;
          background: #b71c1c;
        }
      }
    }
  }

  .#{$alias}data-added {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #16b14e;
    padding: 4px 15px;
    color: #fff;
    font-size: 12px;
    text-transform: uppercase;
    text-align: center;
    border-radius: 4px;
    font-weight: bold;
  }

  .#{$alias}import-block-wrapper {
    background: #212121;
    margin-left: -20px;
    margin-right: -20px;
    margin-bottom: -20px;
    padding: 20px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    margin-top: 40px;

    h3 {
      margin: 0;
      padding: 0;
      color: #fff;
      text-transform: uppercase;
      font-size: 15px;
      font-weight: bold;
      padding-bottom: 15px;
    }

    ul {
      display: grid;
      grid-template-columns: repeat( 10, 1fr);
      grid-gap: 15px;

      li {
        margin: 0;
        padding: 0;
        background: #fff;
        position: relative;
        border-radius: 6px;

        img {
          max-width: 100%;
          width: 100%;
          height: auto;
          padding: 5px;
        }

        h5 {
          padding: 0;
          margin: 0;
          text-align: center;
          padding: 8px 0;
          text-transform: uppercase;
          background: #673AB7;
          color: #fff;
          font-size: 13px;
          font-weight: normal;
          border-radius: 5px;
        }

        .dashicons-no-alt {
          color: #f44336;
          font-size: 25px;
          padding: 2px;
          box-sizing: content-box;
          width: auto;
          height: auto;
          cursor: pointer;
          position: absolute;
          top: 4px;
          right: 4px;

          &:hover {
            background: #f44336;
            color: #fff;
            border-radius: 5px;
          }
        }
      }
    }
  }

  .#{$alias}import-log-wrapper {
    margin-top: 40px;
    margin-left: -20px;
    margin-right: -20px;
    margin-bottom: -20px;

    &.is_hide {
      .#{$alias}import-log-scrollbar {
        display: none;
      }
    }

    .#{$alias}show-log-btn {
      right: 0px;
      background: #3f3976;
      color: #fff;
      padding: 8px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-right: 14px;
      padding-left: 14px;

      span:last-child {
        margin-left: 5px;
        text-transform: uppercase;
      }
      &:hover {
        background: #2672b7;
      }
    }

    .#{$alias}import-log-scrollbar {
      overflow-y: auto;
      max-height: 450px;
      background: #fff;
      padding: 20px;
      display: grid;
      grid-template-columns: 260px 1fr;
      grid-gap: 20px;

      .#{$alias}import-log-anchors-wrapper {
        overflow-y: auto;
        max-height: 410px;
      }
      .#{$alias}import-log-anchors {
        padding: 0;
        margin: 0;

        li {
          border: 1px solid #E0E0E0;
          margin: 0;
          padding: 5px 10px;
          background: #F5F5F5;
          cursor: pointer;
          margin-bottom: -1px;
          overflow: hidden;
          display: grid;
          grid-template-columns: 60px 1fr;
          grid-gap: 10px;

          .#{$alias}asin-total-times {
            border: 1px solid #231f4a;
            line-height: 20px;
            font-size: 10px;
            text-align: center;
            color: #a6a3ba;
            background: #302b5c;
          }

          &.#{$alias}asin-selected {
            background: #3f3976;
            color: #fff;
            border-color: #3f3976;
          }

          &:hover:not(.#{$alias}asin-selected) {
            background: #EEEEEE;
          }

          span:first-child {
            font-size: 12px;
            position: relative;
            bottom: -2px;
          }
          span:last-child {
            display: inline-block;
            font-size: 11px;
            margin-left: 5px;
            background: #FF9800;
            color: #fff;
            padding: 2px 6px;
            border-radius: 4px;
            float: right;

            &.#{$alias}asin-valid {
              background: #43A047;
            }
            &.#{$alias}asin-invalid {
              background: #e53935;
            }
          }
        }

      }
    }

    .#{$alias}import-log {
      list-style: none;
      margin: 0;
      padding: 0;
      display: block;
      max-width: 100%;
      border: 1px solid #E0E0E0;
      padding: 10px;
      overflow-y: auto;
      max-height: 400px;

      p {
        font-size: 12px;
        margin-top: 0;
      }

      .#{$alias}import-time {
        font-weight: bold;
        font-style: italic;
        font-size: 11px;
        margin-right: 10px;
      }

      .#{$alias}import-status-success {
        color: #2E7D32;

         .#{$alias}import-msg {
            a {
               background: #3f3976;
               color: #fff;
               border-radius: 2px;
               padding: 4px;
               cursor: pointer;
               &:hover {
                background: #3a4481;
               }
            }
         }
      }
      .#{$alias}import-status-error {
        color: #c62828;
      }
      .#{$alias}import-status-start {
        font-size: 13px;
      }
    }
  }

  .#{$alias}get-google-chrome {
    text-align: center;
    padding: 60px;

    h1 {
      font-family: 'Roboto', sans-serif;
      font-size: 25px;
      line-height: 25px;
      margin-bottom: 10px;
      padding-top: 15px;
      padding-bottom: 25px;
      display: block;
    }

    img {
      width: 96px;
    }

    a._is_btn {
      background-color: #174ea6;
      color: #fff;
      font-size: 12px;
      padding: 9px 20px;

      &:hover {
        background: darken(#174ea6, 10%);
        color: #fff;
      }
    }
  }

  .#{$alias}get-extension {
    position: relative;
    text-align: center;
    font-weight: bold;

    .#{$alias}loader {
      background: none;
      position: relative;
      display: inline-block;

      .lds-roller {
        position: relative;
        top: 0;
        left: 0;
        margin-left: 0;

        div:after {
          background: #2675b9;
        }
      }
    }

    .#{$alias}get-extension-block {
      width: 320px;
      display: inline-block;
      display: grid;
      grid-template-columns: 80px 1fr;
      grid-gap: 20px;
      margin: 0;
      margin-top: 30px;
      margin-bottom: 10px;
      text-align: left;
      background: #F5F5F5;
      border: 1px solid #E0E0E0;
      padding: 10px;
      border-radius: 4px;

      img {
        width: 80px;
        height: auto;
        background: #fff;
        border-radius: 4px;
      }

      h4 {
        margin-top: 5px;
        font-size: 18px;
      }

      a._is_btn {
        background-color: #174ea6;
        color: #fff;
        font-size: 12px;
        padding: 9px 20px;
        border-radius: 2px;

        &:hover {
          background: darken(#174ea6, 10%);
          color: #fff;
        }
      }
    }
  }

  .#{$alias}validate-left {
    display: block;
    width: 49%;
    float: left;
    p {
      font-size: 15px;
      text-align: left;
    }
    .#{$alias}info-block {
      font-size: 14px;
      background-color: #174ea6;
      color: #fff;
      border-radius: 2px;
      padding: 5px 10px;
    }
    .#{$alias}info-text {
      font-size: 12px;
      color: #7b7b7b;
      font-style: italic;
    }
  }

  .#{$alias}validate-right {
    overflow: hidden;
    display: block;
    float: right;
    width: 49%;
    img {
      width: 100%;
    }
  }

  .#{$alias}validate-extension {
    h2 {
      margin-top: 0;
    }

    form {
      width: 450px;
      margin-top: 20px;

      input {
        width: 100%;
        padding: 10px;
        font-size: 13px;
        margin-bottom: 10px;
      }

      button {
        margin-top: 0px;
        width: 100%;
        background: #305b9b;
        border: none;
        padding: 12px;
        font-size: 13px;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        text-transform: uppercase;

        &:hover {
          background: #3e3d78;
        }

        span {
          font-size: 20px;
          color: #FDD835;
          margin-right: 5px;
        }
      }
    }
  }
}
